import React, {Component, KeyboardEvent} from "react";
import YouTube from "react-youtube";

import "./YoutubeLoop.css";
import {LoopMarkButtons} from "./loop-mark-controls/LoopMarkButtons";
import {TimeInputBox} from "./time-input-box/TimeInputBox";
import { VideoControls } from "./video-controls/VideoControls";
import { ControlIcons } from "./control-icons/ControlIcons";

export type YoutubeLoopProps = {
    title?: string;
    playbackRate: number,
    startSecond: number,
    endSecond: number,
    videoId: string
}

type YoutubeLoopState = {
    playbackRate: number;
    startSecond: number;
    endSecond: number;
    availablePlaybackRates: number[];
    initial: boolean;
    playing: boolean;
    videoId: string;
    videoReady: boolean;
}

export class YoutubeLoop extends Component<YoutubeLoopProps, YoutubeLoopState> {
    static defaultProps = {
        startSecond: 0,
        playbackRate: 1,
        endSecond: 0,
    };

    private player?: YT.Player;
    private timeout?: NodeJS.Timeout;

    private ytLoopRef = React.createRef<HTMLDivElement>()

    constructor(props: YoutubeLoopProps) {
        super(props);
        this.state = {
            ...props,
            initial: true,
            availablePlaybackRates: [1], 
            playing: false,
            videoReady: false
        };

        this.onReady = this.onReady.bind(this);
        this.onStateChange = this.onStateChange.bind(this);
        this.restartVideoSection = this.restartVideoSection.bind(this);

        this.handleChange = this.handleChange.bind(this);
        this.markEnd = this.markEnd.bind(this);
        this.markStart = this.markStart.bind(this);
        this.onReady = this.onReady.bind(this);
        this.onStartChange = this.onStartChange.bind(this);
        this.onEndChange = this.onEndChange.bind(this);
        this.onResetClicked = this.onResetClicked.bind(this);
        this.onPlaybackRateChange = this.onPlaybackRateChange.bind(this);
        this.onRestartLoopClicked = this.onRestartLoopClicked.bind(this);
        this.onVideoSaved = this.onVideoSaved.bind(this);
        this.onPauseVideo = this.onPauseVideo.bind(this);
        this.onPlayVideo = this.onPlayVideo.bind(this);
    }

    componentDidMount() {
    }

    componentDidUpdate(prevProps: Readonly<YoutubeLoopProps>, prevState: Readonly<YoutubeLoopState>, snapshot?: any): void {
        if (prevProps.startSecond !== this.props.startSecond || prevProps.endSecond !== this.props.endSecond || prevProps.playbackRate !== this.props.playbackRate) {
            this.setState({
                startSecond: this.props.startSecond,
                endSecond: this.props.endSecond,
                playbackRate: this.props.playbackRate
            });
            this.player?.setPlaybackRate(this.props.playbackRate);
            this.player?.seekTo(this.props.startSecond, true);
        }

        // state change
        if (this.state.playbackRate !== prevState.playbackRate) {
            this.player?.setPlaybackRate(this.state.playbackRate);
        } else if (this.state.startSecond !== prevState.startSecond) {

        }

        this.ytLoopRef.current?.focus();
        
        
    }

    onReady(event: { target: YT.Player }) {

        this.player = event.target;
        this.setState({availablePlaybackRates: this.player.getAvailablePlaybackRates()});

        this.player.setPlaybackRate(this.props.playbackRate);
        this.player.seekTo(this.state.startSecond, true);
        
        if(this.state.playing) {
            this.player.playVideo();
        } else {
            this.player.pauseVideo();
        }

        if (!this.state.endSecond) {
            this.setState({endSecond: event.target.getDuration()})
        }

        this.setState({videoReady: true})
    }

    onStateChange(event: { target: YT.Player, data: number }) {
       // YT.PlayerState.ENDED

        if (this.timeout) {
            clearTimeout(this.timeout);
            this.setState({playing: false})
        }
        if (event.data === 1) {
            this.player?.setPlaybackRate(1);
            this.player?.setPlaybackRate(this.state.playbackRate);
            const endSecond = this.state.endSecond ? this.state.endSecond : event.target.getDuration();
            this.calculateAndSetTimeout(event.target.getCurrentTime(), endSecond, this.player?.getPlaybackRate());
            this.setState({playing: true})
        }
    }

    onPauseVideo() {
        this.player?.pauseVideo();
    }

    onPlayVideo() {
        this.player?.playVideo();
    }

    calculateAndSetTimeout(startSecond: number, endSecond: number, playbackRate?: number) {
        let duration = endSecond - startSecond;

        if (playbackRate) {
            duration /= playbackRate;
        }
        this.timeout = setTimeout(this.restartVideoSection, duration * 1000);
    }

    restartVideoSection() {
        this.player?.seekTo(this.state.startSecond, true);
    }

    onRestartLoopClicked() {
        this.player?.seekTo(this.state.startSecond, true);
    }

    handleChange(playbackRate: number) {
        const currentTime = this.player?.getCurrentTime();
        //clearTimeout(this.timeout);

        this.player?.setPlaybackRate(playbackRate);
        this.setState({
            initial: false,
            playbackRate});
    }

    onPlaybackRateChange(event: { target: YT.Player, data: number }) {
        if (this.timeout) {
            clearTimeout(this.timeout);
        }
        this.calculateAndSetTimeout(this.player?.getCurrentTime()!, this.state.endSecond, event.data);
    }

    markStart() {
        const currentTime = this.player?.getCurrentTime();
        if (currentTime) {
            this.setState({startSecond: Math.floor(currentTime), initial: false})
        }
    }

    markEnd() {
        if (this.timeout) {
            clearTimeout(this.timeout);
        }
        const currentTime = this.player?.getCurrentTime();
        if (currentTime) {
            this.setState({endSecond: Math.ceil(currentTime), initial: false});
            this.player?.seekTo(this.state.startSecond, true);
        }

    }

    private onStartChange(startSecond: number) {
        if(startSecond > this.player?.getCurrentTime()!) {
            this.player?.seekTo(startSecond, true);
        }

        this.setState({initial: false, startSecond});
    }

    private onEndChange(endSecond: number) {
        if(endSecond <= this.player?.getCurrentTime()!) {
            this.player?.seekTo(this.state.startSecond, true);
        } else {
            if(this.timeout) {
                clearTimeout(this.timeout);
            }
            this.calculateAndSetTimeout(this.player?.getCurrentTime()!, this.state.endSecond, this.state.playbackRate)
        }
        this.setState({endSecond, initial: false});
    }

    private onResetClicked() {
        clearTimeout(this.timeout!);
        if (this.player) {
            this.setState({
                startSecond: 0,
                endSecond: this.player.getDuration(),
                initial: true,
                playbackRate: 1
            });

            this.player.setPlaybackRate(1);
        }
    }

    onVideoSaved() {
        const newVideo: YoutubeLoopProps = {
            startSecond: this.state.startSecond,
            endSecond: this.state.endSecond,
            playbackRate: this.state.playbackRate,
            videoId: this.props.videoId
        }
    }

    render() {
        const onKeyDown = (event: KeyboardEvent) => {
                if(this.ytLoopRef.current === event.target) {
                    if(event.key === ' ') {
                        event.preventDefault();
                        this.state.playing ? this.onPauseVideo() : this.onPlayVideo();
                    } else if(event.key === 'ArrowRight') {
                        event.preventDefault();
                        onVideoRewind(5);
                    } else if(event.key === 'ArrowLeft') {
                        event.preventDefault();
                        onVideoRewind(-5);
                    } else if(event.key === '-') {
                        event.preventDefault();
                        onPlaybackRateChange('decrease');
                    } else if(event.key === '+') {
                        event.preventDefault();
                        onPlaybackRateChange('increase');
                    }
                }
        }

        const onVideoRewind = (seconds: number) => {
            this.player?.seekTo(this.player?.getCurrentTime() + seconds, true)
        }

        const onPlaybackRateChange = (target: 'increase' | 'decrease') => {
            const index = this.state.availablePlaybackRates.indexOf(this.state.playbackRate);
            if(target === 'increase' && index < this.state.availablePlaybackRates.length -1) {
                this.handleChange(this.state.availablePlaybackRates[index + 1]);
            } else if(target === 'decrease' && index > 0) {
                this.handleChange(this.state.availablePlaybackRates[index - 1]);
           }
        }

        const onResetClicked = () => {
            this.setState({
                startSecond: 0,
                endSecond: Math.floor(this.player?.getDuration()!),
                playbackRate: 1
            })
        }

        return <div tabIndex={-1} className="yt-loop"  onKeyDown={event => onKeyDown(event)} ref={this.ytLoopRef} onClick={(event) => {
            if(event.target === this.ytLoopRef.current) {
                this.ytLoopRef.current?.focus();
            }
        }}>
            {/*<FiguresTable figuresTimeFrames={[*/}
            {/*    {name: "First", startSecond: 10, endSecond: 20},*/}
            {/*    {name: "Second", startSecond: 120, endSecond: 130} as FigureTimeFrame,*/}
            {/*    {name: "Third", startSecond: 30, endSecond: 35} as FigureTimeFrame,*/}
            {/*    {name: "Forth", startSecond: 45, endSecond: 55} as FigureTimeFrame,*/}
            {/*    {name: "Forth", startSecond: 45, endSecond: 55} as FigureTimeFrame,*/}
            {/*    {name: "Forth", startSecond: 45, endSecond: 55} as FigureTimeFrame,*/}
            {/*    {name: "Forth", startSecond: 45, endSecond: 55} as FigureTimeFrame,*/}
            {/*    {name: "Forth", startSecond: 45, endSecond: 55} as FigureTimeFrame,*/}
            {/*]} onClicked={this.onClickedFigureTimeFrame}/>*/}
            <div className="yt-loop__container">
                <div className="yt-frame">
                    <YouTube
                        opts={{playerVars: {
                            playsinline: 1
                        }}}
                        videoId={this.props.videoId}
                        onReady={this.onReady}
                        onPlaybackRateChange={this.onPlaybackRateChange}
                        onStateChange={this.onStateChange}/>
                </div>

                <div className="yt-loop__controls">
                    <VideoControls 
                        playing={this.state.playing}
                        onPlayClicked={this.onPlayVideo} 
                        onStopClicked={this.onPauseVideo}
                        onRewindVideo={onVideoRewind}
                        videoReady={this.state.videoReady}
                    />
                    <LoopMarkButtons onMarkStart={this.markStart} onMarkEnd={this.markEnd} onResetClicked={onResetClicked}/>
                    <div className="time-input-boxes">
                        <TimeInputBox value={this.state.startSecond} onChange={this.onStartChange} maxValue={this.state.endSecond!} label={"Loop start (seconds)"}></TimeInputBox>
                        <TimeInputBox value={this.state.endSecond} onChange={this.onEndChange} minValue={this.state.startSecond} maxValue={this.player?.getDuration()!} label={"Loop end (seconds)"}></TimeInputBox>
                    </div>
                    <div className="video-speed">
                        <TimeInputBox value={this.state.playbackRate} onChange={this.handleChange} label="Video speed" possibleValues={this.state.availablePlaybackRates} ></TimeInputBox>
                    </div>
                    
                    <ControlIcons {...this.state}/>    



                    {/*<FrameChanger value={this.state.startSecond} onChange={this.onStartChange}*/}
                    {/*              onButtonClicked={this.markStart} name={"start"}/>*/}
                    {/*<FrameChanger value={this.state.endSecond} onChange={this.onEndChange}*/}
                    {/*              onButtonClicked={this.markEnd} name={"end"}/>*/}
                    {/*<button onClick={this.onRestartLoopClicked}>Restart loop</button>*/}
                    {/* <PlaybackRate handleChange={event => this.handleChange(event.target.value)} value={this.state.playbackRate}
                                  availablePlaybackRates={this.state.availablePlaybackRates}/> */}
                               
                </div>
            </div>
        </div>

    }


}

