import React, {FC} from "react";
import {useLocation, useParams} from "react-router";
import {YoutubeLoop} from "./YoutubeLoop";

export const YoutubeLoopControls: FC = (props) => {
    const { videoId } = useParams<{videoId: string}>();
    const queryParams = new URLSearchParams(useLocation().search)

    return <YoutubeLoop videoId={videoId} startSecond={convert(queryParams.get('s'))}
                        endSecond={convert(queryParams.get('e'))}
                        playbackRate={convert(queryParams.get('pr'))}/>
};


function convert(value: string | null) {
    if(value === null) return undefined;
    const parsedNumber = parseFloat(value);
    if(isNaN(parsedNumber) || !isFinite(parsedNumber) || parsedNumber < 0) return undefined;
    return parsedNumber
}
