import SearchIcon from "@material-ui/icons/Search";
import React, {useState} from "react";
import './SectionSearch.css';


export function SectionSearch({onSearch}: {onSearch: (term: string) => void}) {
    const [inputVisible, setInputVisible] = useState(false)
    const [searchTerm, setSearchTerm] = useState('');

    const onKeyClicked = (key: string) => {
        if(key === 'Escape') {
            setInputVisible(false);
            onSearch('');
        }
    }

    return <div className="section__search">
        <input style={{display: inputVisible ? "block" : "none"}}
               value={searchTerm}
               placeholder="search..."
               onKeyDown={key => onKeyClicked(key.key)}
               onChange={(event) => {
                   setSearchTerm(event.target.value)
                   onSearch(event.target.value);
               }}
        />
        <div className="section__search__icon" onClick={() => {
            setSearchTerm('');
            onSearch('');
            setInputVisible(!inputVisible)}}>
            <SearchIcon/>
        </div>
    </div>
}
