import React, {useEffect, useState} from "react";
import {SectionSearch} from "../home/section-search/SectionSearch";
import {VideoThumbnail} from "./video-thumbnail/VideoThumbnail";
import './VideoList.css';

export interface Video {
    id: string;
    title?: string;
    videoId: string;
    startSecond?: number;
    endSecond?: number;
    playbackRate?: number,
}

interface VideoListProps {
    videoList: Video[];
    sectionTitle: string;
    enabledSearch?: boolean;
    videoRemovable?: boolean;
    onVideoRemove: (videoId: string) => void;
}

export function VideoList({sectionTitle, videoList, enabledSearch, onVideoRemove}: VideoListProps) {
    const [initialVideos, setInitialVideos] = useState<Video[]>(videoList);
    const [videos, setVideos] = useState<Video[]>(videoList);

    useEffect(() => {
        setInitialVideos(videoList);
        setVideos(videos.filter(video => videoList.includes(video)))

    }, [videoList, videoList])

    const search = (term: string) => {
        if(term === '') {
            setVideos(initialVideos);
        } else {
            setVideos(initialVideos.filter(video => video?.title?.includes(term)));
        }
    }

    const onRemoveClicked = (id?: string) => {
        if(id) {
            onVideoRemove(id);
        }
    }

    return <div className="video-list">
        <div className="video-list__title">
            {sectionTitle} ({videos.length})
            {enabledSearch ? <SectionSearch onSearch={search}/> : ''}
        </div>
        <div className="video-list__content">
            {videos.map(video => {
                return <div className="video-list__video-thumbnail">
                    <VideoThumbnail playbackRate={video.playbackRate}
                                    startSecond={video.startSecond}
                                    endSecond={video.endSecond}
                                    title={video.title}
                                    id={video.id}
                                    onRemoveClicked={onRemoveClicked}
                                    videoId={video.videoId}/>
                </div>
            })}
        </div>
    </div>
}
