import './TimeInputBox.css';
import React, {ChangeEvent, KeyboardEventHandler, useEffect, useState} from "react";
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import { ChangeEventHandler } from 'react';
import { MouseEventHandler } from 'react';
import { Select } from '@material-ui/core';

interface TimeInputBoxProps {
    value: number;
    onChange: (value: number) => void;
    maxValue?: number;
    label: string;
    minValue?: number;
    possibleValues?: number[]
}

export function TimeInputBox({value, onChange, maxValue, label, minValue = 0, possibleValues}: TimeInputBoxProps) {
    const [inputValue, setInputValue] = useState<number | string>(value);
    useEffect(() => {
        setInputValue(value);
    }, [value])

    const onPlusClicked = () => {
        if(possibleValues) {
            const index = possibleValues.sort().indexOf(inputValue as number);
            if(index !== undefined && index < (possibleValues.length -1) && index > -1) {
                onChange(possibleValues[index + 1]);
            }
        
        } else {
            if(maxValue && inputValue < maxValue) {
                onChange(inputValue as number + 1)
            } 
        }
    }

    const onMinusClicked = () => {
        if(possibleValues) {
            const index = possibleValues.sort().indexOf(inputValue as number);
            if(index && index > 0) {
                onChange(possibleValues[index - 1]);
            }   
        } else {
            if(minValue !== undefined && (inputValue > minValue)) {
                onChange(inputValue as number - 1)
            }
         } 
    }

    const onInputChanged: ChangeEventHandler<HTMLInputElement> = (event) => {
        if(event.target.value.trim() === '') {            
            setInputValue(' ');
            return;
        }

        const parsedValue = parseFloat(event.target.value);
        if(!isNaN(parsedValue)) {
            setInputValue(parsedValue);
        }
    }

    const onSubmit = () => {
        if(inputValue === ' ') {
            setInputValue(value);
            return;
        }

        if(possibleValues && possibleValues.indexOf(inputValue as number) > -1) {
            onChange(inputValue as number);
        } else if(!possibleValues && maxValue) {
            if(inputValue < maxValue && inputValue >= minValue) {
                onChange(inputValue as number   )
            } else {
                setInputValue(value);
            }
        } else {
            setInputValue(value);
        }
    }



    return <div className="time-input-box">
        <label>{label}</label>
        <div className="time-input-box__content">
            <button className="time-input-box__minus" onClick={onMinusClicked}>
                <RemoveIcon/>
            </button>
            <input type="number" min={minValue} step="0.01" value={inputValue} onChange={onInputChanged} onKeyDown={event => event.key === 'Enter' && onSubmit()}
                onFocus={event => event.target.select()}
                onBlur={() => onSubmit()}/>
            <button className="time-input-box__plus" onClick={onPlusClicked}>
                <AddIcon/>
            </button>
        </div>
    </div>
}
