import React from 'react';
import './Help.css';
import YTVideoImg from './yt-video.png';
import MobileYTVideoImg from './mobile-yt-video.png';
import MobileVideoList from './video-list.png';
import SearchMobile from './search-mobile.png';

export default function Help() {
    return <div className="help">
        <div className="help__content">
            <h2>1. What is this application for?</h2>
            <p><strong>Loop Video Fragment</strong> is an application for looping and storing fragments of youtube video. You can install this on your phone (iOS and Android).</p>
            <div className="help__content__mobile-images">
                <img className="help__content__img--mobile" src={MobileYTVideoImg}/>
                <img className="help__content__img--mobile" src={MobileVideoList}/>
                <img className="help__content__img--mobile" src={SearchMobile}/>
            </div>
            {/* <img className="help__content__img" src={YTVideoImg}/> */}
            <h2>2. How to install on mobile phone?</h2>
            <p><strong>For Android - </strong> when site will be opened for the first time you will see popup - <strong>"Add to home screen"</strong>. If you missed, discarded or from some reason did not got popup you still can install it by going to the settings =&gt; <strong>"Add to home screen"</strong>"</p>
            <p><strong>For iOS - </strong> to install it on iphones you have to go directly to the settings then chose <strong>Add to home screen</strong></p>

            <h2>3. Contact</h2>
            <p>Email address - hadson172@gmail.com</p>
        </div>
    </div>
}