import React from 'react';
import './App.css';
import {Route, Switch} from "react-router";
import {YoutubeLoopControls} from "./youtube-display/YoutubeLoopControls";
import {Header} from "./header/Header";
import {Home} from "./home/Home";
import {Search} from "./search/Search";
import { BrowserRouter, HashRouter } from 'react-router-dom';
import Help from './help-page/Help';

type YoutubeLoopRouteParams = {
    id: string;
}


function App() {
  return (
      <HashRouter>
    <div className="app">
        <Header/>
        <Switch>
            <Route path="/video/:videoId">
                    <YoutubeLoopControls/>
            </Route>
            <Route path="/search">
                <Search/>
            </Route>
            <Route path="/help">
                <Help/>
            </Route>
            <Route path="/">
                <Home/>
            </Route>
        </Switch>
    </div>
    </HashRouter>
    
  );
}

export default App;
