import React, { useState } from "react";
import "./Home.css";
import { VideoList } from "../video-list/VideoList";
import { getAllVideos, deleteVideo } from "../data/video-service";
import InfoBox from "./info-box/InfoBox";
import SaveIcon from "@material-ui/icons/Save";

export function Home() {
  const [videoList, setVideoList] = useState(getAllVideos());

  console.log(videoList);

  const onVideoRemove = (id: string) => {
    deleteVideo(id);
    setVideoList(videoList.filter((video) => video.id !== id));
  };

  return (
    <div className="home">
      <VideoList
        videoList={videoList}
        onVideoRemove={onVideoRemove}
        sectionTitle="Saved videos"
        enabledSearch={true}
      />
      {videoList.length === 0 && (
        <div className="home__info-box">
          <InfoBox>
            <div className="home__info-box__content" onClick={event => event.preventDefault()}>
              No videos saved. Use search bar by typing direct youtube link or search phase.
              When video will open you can save it by - 
              <div className="home__info-box__icon">
                <SaveIcon style={{ color: "black" }} />
              </div>
            </div>
          </InfoBox>
        </div>
      )}
    </div>
  );
}
