import React from "react";
import './LoopMarkButtons.css';

interface LoopMarkButtonsProps {
    onMarkStart: () => void;
    onMarkEnd: () => void;
    onResetClicked: () => void;
}

export function LoopMarkButtons({onMarkStart, onMarkEnd, onResetClicked}: LoopMarkButtonsProps) {
    return <div className="loop-mark-buttons">
        <button className="loop-mark-buttons__start" onClick={onMarkStart}>Mark loop start</button>
        <button className="loop-mark-buttons__reset" onClick={onResetClicked}>Reset</button>
        <button className="loop-mark-buttons__end" onClick={onMarkEnd}>Mark loop end</button>
    </div>
}
