import {Video} from "./Video";

const LOCAL_STORAGE_KEY = 'dnc-videos';

export function saveVideo(video: Video) {
    let videosList: Video[] = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY) ?? '[]');
    videosList = [video, ...videosList];
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(videosList));
}

export function getAllVideos(): Video[] {
    return JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY) ?? '[]');
}

export function deleteVideo(id: string) {
    const videoList = getAllVideos().filter(video => video.id !== id);
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(videoList));
}

