import React, {useEffect, useState} from "react";
import CloseSign from "../../../utils/close-sign/CloseSign";
import "./Modal.css";

export function Modal({visible, onModalClose, children, headerTitle}: {visible: boolean, onModalClose: () => void, children: any, headerTitle: string}) {
    // @ts-ignore
    const onEscClicked = (event) => {
        if(event.key === 'Escape') {
            onModalClose();
        }
    }

    useEffect(() => {
        window.addEventListener('keydown', onEscClicked)
        return () => window.removeEventListener('keydown', onEscClicked)
    }, []);


    return <div className="modal" style={{display: visible ? 'block' : 'none'}}>
        <div className="modal__backdrop"></div>
        <div className="modal__content">
            <div className="modal__header">
                <div className="modal__title">{headerTitle}</div>
                <CloseSign onClicked={onModalClose}/></div>
                {children}
        </div>
    </div>
}
