import React, { useState } from "react";
import "./ControlIcons.css";
import SettingsIcon from '@material-ui/icons/Settings';
import LinkIcon from '@material-ui/icons/Link';
import { SaveVideo } from "../save-video/SaveVideo";
import copy from 'copy-to-clipboard';
import { useLocation } from "react-router";

interface ControlIconsProps {
    videoId: string;
    startSecond: number;
    endSecond: number;
    playbackRate: number;
    location?: string;
}

export function ControlIcons({videoId, startSecond, endSecond, playbackRate, location}: ControlIconsProps) {
    const [ref, setRef] = useState<HTMLSpanElement | null>(null);
  

    const copyLinkToLoop = () => {
      console.log(window.location);
        const url = new URL(`/#/video/${videoId}`, window.location.origin).toString() + `?s=${startSecond}&e=${endSecond}&pr=${playbackRate}`;
        if( copy(url) ) {
                if(ref) {
                    ref.style.display = "inline";
                    setTimeout(() => {
                        ref.style.display = "none";
                    }, 1000)
                }
            }
    }

  return (
    <div className="control-icons">
      <div className={"settings-icon"}>
        <SettingsIcon fontSize={"large"} />
        Settings
      </div>
      <SaveVideo
        videoId={videoId}
        startSecond={startSecond}
        endSecond={endSecond}
        playbackRate={playbackRate}
      />
      <div>
            <div className="copy-link-icon" onClick={copyLinkToLoop}>
                <span ref={r => setRef(r)} className="copy-link-icon__prompt">Copied!</span>
                <LinkIcon fontSize={"large"} />
                Copy loop link
            </div>
      </div>
    </div>
  );
}