import './Search.css';
import React, {useEffect, useState} from "react";
import {useHistory, useLocation} from "react-router";
import {searchYoutube} from "./youtube-search";
import {VideoThumbnail} from "../video-list/video-thumbnail/VideoThumbnail";
import {YouTubeSearchResults} from "youtube-search";

export function Search() {
    let history = useHistory();
    const searchTerm = new URLSearchParams(useLocation().search).get('q');
    const [searchResult, setSearchResult] = useState<YouTubeSearchResults[]>([])
    useEffect(() => {
        if(searchTerm) {
            searchYoutube(searchTerm).then(result => {
                setSearchResult(result.results.filter(item => item.kind === 'youtube#video'));
            }).catch(err => console.log(err))
        }
    }, [searchTerm])


    if(searchTerm === null) {
        history.push("/");
    }

    return <div className="search">
        {searchResult.map(video => {
            return <div className="search__video-thumbnail">
                <VideoThumbnail videoId={video.id} title={video.title} thumbnail={video.thumbnails.standard?.url}/>
            </div>
        })}
    </div>
}
