import SaveIcon from "@material-ui/icons/Save";
import React, {useEffect, useState} from "react";
import './SaveVideo.css'
import {Modal} from "./modal/Modal";
import {saveVideo} from "../../data/video-service";
import { v4 } from "uuid";

interface VideoToSave {
    videoId: string;
    startSecond: number;
    endSecond: number;
    playbackRate: number,
}

export function SaveVideo(video: VideoToSave) {
    const [modalVisible, setModalVisible] = useState(false);
    const [title, setTitle] = useState('');

    const onSaveClicked = () => {
        saveVideo({...video, title, id: v4()})
        setModalVisible(false);
    }
    // @ts-ignore
    const onEnterClicked = (event) => {
        console.log("clicked", event.key, event.key === 'Enter' && modalVisible)
        if(event.key === 'Enter' && modalVisible) {
            onSaveClicked();
        }
    }


    return <div className="save-video">
        <Modal visible={modalVisible}
        headerTitle={"Save video as..."}
        onModalClose={() => {
            setTitle('');
            setModalVisible(false);
        }}>
            <div className="save-video-modal-content" onKeyDown={onEnterClicked}>
                <label className="save-video-modal-content__title" >
                     <span>Title</span>
                     <input type="text" placeholder="title" value={title} onChange={event => setTitle(event.target.value)}/>
                </label>

                <div><button onClick={onSaveClicked}>Save</button></div>
            </div>
        </Modal>
        <div className="save-video__icon" onClick={() => setModalVisible(true)}>
            <SaveIcon fontSize={"large"} />
            Save
        </div>
    </div>
}
