import React, { useEffect, useState } from "react";
import './VideoControls.css'
import PlayArrowIcon from "@material-ui/icons/PlayArrow";
import PauseIcon from '@material-ui/icons/Pause';
import Replay5Icon from "@material-ui/icons/Replay5";
import Forward5Icon from "@material-ui/icons/Forward5";

interface VideoControlsProps {
  playing: boolean;
  videoReady: boolean;
  onPlayClicked: () => void;
  onStopClicked: () => void;
  onRewindVideo: (count: number) => void;
}

export function VideoControls({playing, onPlayClicked, onStopClicked, onRewindVideo, videoReady}: VideoControlsProps) {
  const [videoPlaying, playVideo] = useState(playing);
  useEffect(() => {
    playVideo(playing);    
   }, [playing]);

  return (
    <div>
      <div className="video-controls">
        <div onClick={() => videoReady && onRewindVideo(-5)}>
            <Replay5Icon fontSize={"large"} color={videoReady ? 'inherit' : 'disabled'} />
        </div>
        <div onClick={videoPlaying ? () => videoReady && onStopClicked() : () => onPlayClicked()}>
          {videoPlaying ? <PauseIcon fontSize={'large'} color={videoReady ? 'inherit' : 'disabled'}/> : <PlayArrowIcon fontSize={'large'} color={videoReady ? 'inherit' : 'disabled'}/>}
        </div>
        <div onClick={() =>videoReady && onRewindVideo(5)}>
            <Forward5Icon fontSize={"large"} color={videoReady ? 'inherit' : 'disabled'}/>
        </div>
      </div>
    </div>
  );
}
