import React from "react";
import {useHistory, useLocation} from "react-router";
import CloseSign from "../../utils/close-sign/CloseSign";
import './VideoThumbnail.css';

interface VideoThumbnail {
    id?: string;
    title?: string;
    thumbnail?: string
    playbackRate?: number,
    startSecond?: number,
    endSecond?: number,
    videoId: string
    onRemoveClicked?: (id?: string) => void;
}

export function VideoThumbnail(video: VideoThumbnail) {
    const history = useHistory();
    const onRemoveClicked = () => {
        if(video.onRemoveClicked) {
            video.onRemoveClicked(video.id)
        }
    }


    const handleClick = () => {
        const url = new URL(`https://www.example.com/video/${video.videoId}`)
        appendQueryParams(url, 's', video.startSecond);
        appendQueryParams(url,'e', video.endSecond);
        appendQueryParams(url,'pr', video.playbackRate);
        const resultUrl = url.toString().replace('https://www.example.com', '');
        history.push(resultUrl);
    }
    return <div className="video-thumbnail">
        <div className="video-thumbnail__item">
            {video.onRemoveClicked && <div className="video-thumbail__item__cover__wrapper">
                <CloseSign onClicked={onRemoveClicked}/>
                <div className="video-thumbail__item__cover" onClick={() => handleClick()}></div>
            </div>}

            <img src={video.thumbnail ?? `https://img.youtube.com/vi/${video.videoId}/0.jpg`}  onClick={() => handleClick()}/>
        </div>

        <div className="video-thumbnail__title">
            {video.title}
        </div>
    </div>
}


function appendQueryParams(url: URL, name: string, value: number | undefined) {
    if (value) {
        url.searchParams.append(name, value.toString());
    }
}
