import SearchIcon from "@material-ui/icons/Search";
import React, {KeyboardEventHandler, useEffect, useState} from "react";
import './Search.css';
import {useHistory} from "react-router";

const youtubeLinks = [
    new RegExp('.*youtu.be/([a-zA-Z0-9_-]*)\\?.*'),
    new RegExp('.*youtu.be/([a-zA-Z0-9_-]*)'),
    new RegExp('.*youtube.com\/watch\\?v=([a-zA-Z0-9_-]+)')
]

export function Search() {
    const [inputContent, setInputContent] = useState('');

    let history = useHistory();
    useEffect(() => {
        history.listen(location => {
            if(!location.pathname.startsWith('/search') && !location.pathname.startsWith('/video')) {
                setInputContent('');
            }
        })
    }, []);
    const onButtonClicked = () => {
        let result = youtubeLinks.map(link => {
            console.log(link.exec(inputContent))
            return link.exec(inputContent)?.[1];
        }).find(match => match);
        if (result) {
            history.push(`/video/${result}`)
        } else {
            if(inputContent?.trim()) {
                history.push(`/search?q=${inputContent}`)
            }
        }
    }
    const onEnterClicked: KeyboardEventHandler<HTMLInputElement> = event => {
        if(event.key === 'Enter') {
            onButtonClicked();
        }
    }

    return <div className="header__search">
            <input placeholder='youtube link or search phase...'
                   value={inputContent}
                   onChange={(event) => setInputContent(event.target.value)}
                   onKeyDown={onEnterClicked}/>
            <div className="header__search__icon" onClick={() => onButtonClicked()}><SearchIcon fontSize={"large"}/></div>
        </div>
}
